<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <div class="title">
          <b>{{ article.title }}</b>
          <div>
            <b>作者：</b>{{ article.teacher }}
            <b>时间：</b>{{ article.time }}
            <b>售价：</b>{{ article.price }}
          </div>
        </div>
      </div>
      <div>
        <share :config="config" data-disabled="google,twitter,facebook,linkedin,douban,tencent,diandian" data-qq-title="好的源码，大家分享"></share>
        <div class="introduce">
          <b>介绍 </b>{{ article.remark }}
        </div>
        <div v-for="(o) in article.json" :key="o">
          <div class="img" v-if="o.type === 'img'">
            <img :src="o.content"/>
          </div>
          <div class="img_small" v-if="o.type === 'img_small'">
            <el-row :gutter="24">
              <div v-for="(i) in o.content" :key="i">
                <el-col :span="8">
                  <img :src="i"/>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="img_big" v-if="o.type === 'img_big'">
            <el-row :gutter="24">
              <div v-for="(i) in o.content" :key="i">
                <el-col :span="24">
                  <img :src="i"/>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="text" v-if="o.type === 'text'">
            {{o.content}}
          </div>
        </div>
      </div>
      <div>
        <p v-if="proArticle != null">上一篇：
          <a :href="'/#/article?id=' + proArticle.id" target="_Blank"><span>{{proArticle.title}}</span></a>
        </p>
        <p v-if="nextArticle != null">下一篇：
          <a :href="'/#/article?id=' + nextArticle.id" target="_Blank"><span>{{nextArticle.title}}</span></a>
        </p>
      </div>
      <div class="introduce">
          <b>声明：</b> 版权所有，盗版必究。
          <!-- 博客地址：
          <div v-for="(o) in article.link" :key="o">
            <router-link :to="o.url">{{o.title}}</router-link>
            <a href="o.url" target="view_window">{{o.title}}</a>
          </div> -->
      </div>
    </el-card>
    <v-scroll></v-scroll>
  </div>
</template>

<script>
import vScroll from '../page/Scroll.vue'
import { article } from '../../../static/js/article.js'

export default {
  data () {
    return {
      id: this.$route.query.id,
      article: article.data[this.$route.query.id - 1],
      proArticle: article.data[this.$route.query.id - 2],
      nextArticle: article.data[this.$route.query.id]
    }
  },
  components: {
    vScroll
  }
}
</script>

<style scoped>
.img img {
  width: 100%;
  height: 100%;
  border: "1";
  border-width: 1px;
  border-style: solid;
  margin-top: 10px;
}
.img_small img {
  width: 100%;
  height: 700px;
  border: "1";
  border-width: 1px;
  border-style: solid;
  margin-top: 10px;
}
.img_big img {
  width: 100%;
  height: 500px;
  border: "1";
  border-width: 1px;
  border-style: solid;
  margin-top: 10px;
}
.text {
  text-indent:25px;
}
.title {
  font-size: 20px;
}
.title div {
  font-size: 15px;
  color: red;
  margin-top: 10px;
}
.introduce {
  padding: 10px;
  background-color: #EFF2F7;
  margin-bottom: 10px;
  border: solid 1px #F3F3F3;
  line-height: 23px;
}
</style>



